export default {
	data() {
		return {
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			userInformation: {
				'headimg': '',
				'nickname': ''
			}
		}
	},
	methods: {
		wxGetUserInfo: function() {
			let that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							console.log(infoRes);
							// prevPage.setData({
							// 	'userInformation': {
							// 		'headimg': infoRes.userInfo.avatarUrl,
							// 		'nickname': infoRes.userInfo.nickName;
							// 	}
							// })
							// that.userInformation.headimg = infoRes.userInfo.avatarUrl;
							// that.userInformation.nickname = infoRes.userInfo.nickName;
							that.saveUserInfo(infoRes.userInfo);
						}
					});
				}
			});
		},
		saveUserInfo: function(userInfo) {
			this.$http.post('/api/user/saveBase', userInfo).then(res => {
				var data = res.data.data
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					uni.showToast({
						title: '授权成功！',
						icon: 'none'
					});
					uni.navigateBack();
				}
			}).catch(err => {
				console.log(err);
			})
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			if (!this.userInfo) {
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		}
	}
}
